nav { 
    position: relative;
    min-width: 200px;
    z-index: 2;
    text-align: center;
    background-color: #fff;
    color:#707070;
    width: 12%;
    border-right: .5px solid;
}
nav.dark{
    background-color: #707070;
    color:#fff;
}

.version-list_toggle-button {
    display: none;
    width: 100%;
    padding: 0.8125rem 1.6875rem;
    color: inherit;
    background-color: inherit;
    text-align: left;
    cursor: pointer;
    font-size: 1rem;
}
.version-list_toggle-indicator {
    float: right;
}

.version-list {
    display: block;
    list-style: none;
    margin: 0;
    padding: 0;
    flex-direction: column;
    font-size: 1rem;
    overflow-wrap: break-word;
}
.version-list.open{
    display: block;
    position: absolute;
    background-color: inherit;
    color: inherit;
    height: auto;
    width: 100%;
}

.version-list li:hover {
    background-color: #ccc;
}

.version-list li {
    display: block;
    padding: .5rem;
    text-decoration: none;
    color: inherit;
    cursor: pointer;
}

@media(max-width:1024px){
    nav {
        width: 100%;
        background-color: #707070;
        color: #fff;
        border: none;
    }
    nav.dark{
        background-color: #fff;
        color:#707070;
    }
    .version-list_toggle-button{
        display: block;
        border: none
    }
    .version-list_toggle-button::after{
        display: block;
        height: 1px;
        width: 50px;
        background-color: #fff;
    }

    .version-dropdown {
        display: none;
    }

    .version-list {
        display: none;
        height: 0;
        transition: height .8s ease-in;
        width: 100%;
        font-size: 1.1rem;
        box-shadow: 1px 1px 7px #404040 inset;
    }

    .version-list.open{
        display: block;
        position: relative;
        height: auto;
    }
}