.resp-container {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
}

.resp-container__top {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin: 0 1.6875rem 
}

.isPrev{
    font-size: .8rem;
    font-weight: bolder;
    text-transform: uppercase; 
}
.isPrev.hidden {
    visibility: hidden;
}
.resp-iframe {
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    margin-top: 2rem; 
    left: 0;
    right: 0;
    height: 100%;

}

@media(max-width: 1024px){
    .resp-container {
        padding-bottom: 75%;
    }
}