.dark-mode {
    position: fixed;
    top:150px;
    right: 1.6875rem;
    font-size: 0;
    border: none;
    width: 3rem;
    height: 3rem;
    border-radius: 2rem;
    background: url('../../img/nights_stay-24px.svg') rgba(0,0,0,0) no-repeat center;
    transition: background-color .25s ease-in;
    outline: none;
    z-index: 2;
}
.dark-mode:hover {
    background-color: rgba(0,0,0,.2);
}

.dark-mode.active {
    background: url('../../img/wb_sunny-24px.png') no-repeat center;
}
.dark-mode.active:hover {
    background-color: rgba(255,255,255,.2);
}

@media(max-width:1024px){
    .dark-mode {
        bottom: 12px;
        top: unset;
    }
}

