@font-face {
  font-family: 'Frank New';
  src: url('./fonts/FrankNew-Thin.woff2') format('woff2'),
      url('./fonts/FrankNew-Thin.woff') format('woff'),
      url('./fonts/FrankNew-ThinItalic.woff2') format('woff2'),
      url('./fonts/FrankNew-ThinItalic.woff') format('woff');
  font-weight: 100;
  font-style: italic;
}

body {
  margin: 0;
  min-width: 320px;
  font-family: 'Frank New',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body.dark {
  background-color: #707070;
}

/* code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
} */
.resp {
  display: flex;
}

.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

h2 {
  margin-left: 1.6875rem;
}


@media(max-width:1024px){
  .resp {
    display: block;
  }
}